/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
summary,
textarea {
	font-family: "Open Sans", sans-serif;
	font-size: 1rem;
	line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	font-family: CircularStdBlack, "sans-serif";
}

h1 {
	font-size: 1.5rem;
}

h2 {
	font-size: 1.375rem;
}

h3 {
	font-size: 1.25rem;
}

h4 {
	font-size: 1.125rem;
}

h5 {
	font-size: 1rem;
}

h6 {
	font-size: 1rem;
}

p {
	margin-bottom: 1.5em;
	font-family: "Open Sans", sans-serif;
	font-size: 1rem;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", courier, monospace;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	color: #0b2134;
	background-color: unset;
	text-decoration: none;
}
mark:hover,
ins:hover {
	color: #557ea5;
}

big {
	font-size: 125%;
}

@font-face {
	font-family: CircularStdBlack;
	src: url("./fonts/circular-converted/CircularStd-Black.woff2") format("woff2");
	font-weight: normal;
	font-display: swap;
}
@font-face {
	font-family: CircularStdBold;
	src: url("./fonts/circular-converted/CircularStd-Bold.woff2") format("woff2");
	font-weight: normal;
	font-display: swap;
}
@font-face {
	font-family: CircularStdBook;
	src: url("./fonts/circular-converted/CircularStd-Book.woff2") format("woff2");
	font-weight: normal;
	font-display: swap;
}
/* Elements
--------------------------------------------- */
body {
	background: #fff;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

