}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
#page {
	max-width: 100%;
	margin: 0 auto 5rem;
	overflow: hidden;
}

@media screen and (min-width: 45.625rem) {
	#page {
		margin: 0 auto;
	}
}
/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Navigation
--------------------------------------------- */
#mobile-navigation {
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: #0b2134;
	z-index: 90001;
	height: 5.2813rem;
	border-top: 2px solid #e6e6e6;
}
#mobile-navigation ul {
	padding-left: 0;
	margin: 0;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	place-items: center;
}
#mobile-navigation ul li {
	list-style: none;
}
#mobile-navigation ~ .widget {
	margin: 0;
	display: block;
	position: fixed;
	left: 0;
	right: 0;
	bottom: -5rem;
	z-index: 9000;
	transition: all 0.2s ease-in-out;
	font-family: CircularStdBold, "sans-serif";
}
#mobile-navigation ~ .widget label {
	display: none;
}
#mobile-navigation .mobile-cart {
	position: relative;
}
#mobile-navigation .mobile-cart svg {
	fill: #e6e6e6;
}
#mobile-navigation .mobile-cart svg:hover {
	fill: #557ea5;
}
#mobile-navigation .mobile-cart .count {
	color: white;
	background-color: #01315f;
	border-radius: 50%;
	display: block;
	position: absolute;
	top: -1px;
	right: 9px;
	padding: 0.1rem 0.6rem;
	font-weight: bolder;
	font-family: CircularStdBold, "sans-serif";
}
#mobile-navigation ~ .menu-footer-extended-menu-container {
	margin: 0;
	display: block;
	position: fixed;
	left: 0;
	right: 0;
	bottom: -30rem;
	z-index: 9000;
	transition: all 0.6s ease-in-out;
	font-family: CircularStdBook, "sans-serif";
	background-color: #0b2134;
	font-size: 1.35rem;
	padding: 1rem;
}
#mobile-navigation ~ .menu-footer-extended-menu-container a {
	text-decoration: none;
	color: #e6e6e6;
}
#mobile-navigation ~ .menu-footer-extended-menu-container a:hover {
	color: #557ea5;
}
#mobile-navigation ~ .menu-footer-extended-menu-container ul {
	padding-left: 0;
	margin: 0;
}
#mobile-navigation ~ .menu-footer-extended-menu-container ul li {
	list-style: none;
}
#mobile-navigation ~ .menu-footer-extended-menu-container ul.sub-menu {
	padding-left: 2rem;
}
#mobile-navigation li button {
	padding: 0;
	border: unset;
	background: unset;
	cursor: pointer;
}
#mobile-navigation li button,
#mobile-navigation li a {
	color: #e6e6e6;
	padding: 1rem;
}
#mobile-navigation li button svg,
#mobile-navigation li a svg {
	width: 40px;
	height: 40px;
}
#mobile-navigation li button svg:hover,
#mobile-navigation li a svg:hover {
	color: #557ea5;
}
#mobile-navigation.search-open ~ .widget {
	bottom: 5.2813rem;
	z-index: 9001;
}
#mobile-navigation.search-open ~ .widget .wc-block-product-search__button {
	margin: 0;
	padding: 0.8rem;
	background-color: white;
	color: #0b2134;
	border: unset;
	border-radius: 0;
	border-left: 1px solid rgba(128, 128, 128, 0.1);
}
#mobile-navigation.search-open ~ .widget #wc-block-search__input-1 {
	border: unset;
	border-radius: 0;
}
#mobile-navigation li.mobile-hamburger button svg {
	width: 50px;
	height: 50px;
}
#mobile-navigation.menu-open ~ .menu-footer-extended-menu-container {
	bottom: 5rem;
	z-index: 9001;
}

#site-navigation {
	display: none;
	left: 0;
	right: 0;
	position: relative;
}
#site-navigation .menu-header-menu-container {
	position: relative;
