}
#site-navigation .product_list_widget {
	gap: 1rem;
}
#site-navigation #menu-header-menu {
	display: flex;
	justify-content: center;
	gap: 0.5rem;
}
#site-navigation .menu {
	background-color: #0b2134;
}
#site-navigation .menu li a {
	padding: 3rem 1rem 2rem 1rem;
	width: 100%;
