	text-decoration: none;
	background-color: #0b2134;
	color: white;
	display: block;
	transition: background-color 0.1s ease-in-out;
}
#site-navigation .menu li a:hover, #site-navigation .menu li a:focus, #site-navigation .menu li a:active {
	filter: brightness(1.2);
