.woocommerce-EditAccountForm fieldset span {
	display: block;
}

.woocommerce-form-login .woocommerce-form-row {
	display: inline-block;
	justify-content: center;
	width: 15rem;
	margin-left: 5rem;
}
.woocommerce-form-login label {
	display: inline-block;
	width: 100%;
	padding-bottom: 0.5rem;
	text-align: left;
}
@media screen and (min-width: 24.4375rem) {
	.woocommerce-form-login label {
		text-align: left;
	}
}
.woocommerce-form-login em {
	display: block;
}
.woocommerce-form-login button {
	margin-top: 1.5rem;
	font-family: CircularStdBook, "sans-serif";
	background-color: #b7ae2b;
	font-size: 1rem;
	color: white;
	border-radius: 9px;
	padding: 1rem 1.5rem;
	text-align: center;
	display: inline-block;
	width: max-content;
	text-decoration: none;
	transition: all 0.1s ease-in-out;
	float: right;
	width: 10rem;
	filter: brightness(1.1);
}
.woocommerce-form-login button:hover {
	filter: brightness(1);
	color: white;
}
.woocommerce-form-login button:active {
